import { useAuth } from "../contexts/authContext";
import { useModal } from "../contexts/modalContext";
import Button from "./Button";
import { FaPowerOff } from "react-icons/fa";

const Logout = () => {
  const { closeModal } = useModal();
  const { logout } = useAuth();

  const handleLogout = () => {
    logout();
    closeModal();
  };

  return (
    <div className="sm:w-100 bg-white rounded-xl p-12 space-y-12">
      <div className="HEAD space-y-2">
        <span className="block absolute right-1/2 translate-x-1/2 top-0 -translate-y-1/2 w-max bg-primeRed p-4 rounded-full text-white">
          <FaPowerOff size={32} />
        </span>
        <p className="font-medium text-center">Logout Account</p>
        <p className="text-sm font-medium text-primary text-center">
          Are you sure you want to logout?
        </p>
      </div>

      <div className="BTN-SECTION flex justify-center">
        <Button
          onClick={handleLogout}
          className="px-6 py-3 rounded-tl-md rounded-bl-md"
        >
          yes, logout!
        </Button>
        <Button
          reverse
          onClick={() => closeModal()}
          className="px-6 py-3 rounded-tr-md rounded-br-md"
        >
          cancel
        </Button>
      </div>
    </div>
  );
};

export default Logout;
