import { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { useModal } from "../../contexts/modalContext";
import Logout from "../../components/Logout";
import useShare from "../../hooks/useShare";
import { useReferral } from "../../services/userServices";

const Tab = ({ icon, text, url }) => {
  const location = useLocation();
  const [isSelected, setIsSelected] = useState(false);
  const [isLocation, setIsLocation] = useState(false);
  const { openModal } = useModal();
  const { shareContent } = useShare();
  const { data: referralCode } = useReferral();

  useEffect(() => {
    if (location.pathname.includes(url)) setIsLocation(true);
  }, [url, location.pathname]);

  const handleReferralClick = async () => {
    if (referralCode) {
      shareContent({
        title: "Join us and get rewards!",
        text: "Use my referral link to sign up.",
        url: referralCode,
      });
    }
  };

  const handleClick = () => {
    if (text === "logout") openModal(<Logout />);
    if (text === "refer & earn") handleReferralClick();
  };

  return (
    <Link
      to={url}
      onMouseEnter={() => setIsSelected(true)}
      onMouseLeave={() => setIsSelected(false)}
      onClick={handleClick}
      className={`relative py-2 max-lg:py-3 px-6 flex items-center gap-4 shadow ${
        isSelected || isLocation ? "bg-white" : "bg-offWhite"
      }`}
    >
      <div
        className={`${
          isSelected || isLocation ? "text-primeRed" : "lg:text-primary"
        }`}
      >
        {icon}
      </div>
      <p
        className={`capitalize font-medium ${
          isSelected || isLocation ? "text-primeRed" : "lg:text-primary"
        }`}
      >
        {text}
      </p>

      <span
        style={{
          transform: isSelected || isLocation ? "scaleY(1)" : "scaleY(0)",
        }}
        className="absolute h-full w-[2px] bg-primeRed left-0 scale-y-0"
      />
    </Link>
  );
};

export default Tab;
