import { useState, useCallback } from "react";
import { useModal } from "../contexts/modalContext";
import InputBox from "../components/InputBox";
import { IoClose } from "react-icons/io5";
import { useToast } from "../contexts/toastContext";

const useShare = () => {
  const [isSharing, setIsSharing] = useState(false);
  const { openModal, closeModal } = useModal();
  const toast = useToast();

  const copyToClipboard = useCallback(
    async (content) => {
      try {
        await navigator.clipboard.writeText(content);
        toast.success("Link copied to clipboard!");
      } catch (error) {
        console.error("Failed to copy text:", error);
        toast.error("Failed to copy link. Please try manually.");
      }
    },
    //eslint-disable-next-line
    [openModal]
  );

  const handleNonNativeShare = useCallback(
    ({ title, text, url }) => {
      openModal(
        <div className="relative max-w-80 w-svw bg-white p-4 space-y-4">
          <h3 className="font-medium">{title}</h3>
          <p className="text-sm">{text}</p>
          <InputBox
            value={url}
            readOnly
            className="px-4 py-2 border border-dark rounded-md text-sm font-medium"
          />
          <button
            onClick={() => copyToClipboard(url)}
            className="w-full bg-blue-500 px-4 py-2 rounded-md text-sm text-white font-medium"
          >
            Copy Link
          </button>
          <button
            onClick={closeModal}
            className="inline-block absolute -top-2 right-2"
          >
            <IoClose size={18} />
          </button>
        </div>
      );
    },
    [openModal, closeModal, copyToClipboard]
  );

  const shareContent = useCallback(
    async ({ title, text, url, files }) => {
      if (navigator?.share) {
        try {
          setIsSharing(true);
          const shareData = { title, text, url };

          if (navigator.canShare && files && navigator.canShare({ files })) {
            shareData.files = files;
          }

          await navigator.share(shareData);
        } catch (error) {
          console.error("Error during native sharing:", error);
        } finally {
          setIsSharing(false);
        }
      } else {
        handleNonNativeShare({ title, text, url });
      }
    },
    [handleNonNativeShare]
  );

  return { shareContent, isSharing };
};

export default useShare;
